<template>
  <div class="container">
    <div class="row">
      <h4>{{ $route.query.msg }}</h4>
    </div>
    <h1>{{ $t('login.title') }}</h1>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <form>
          <div class="form-group text-left">
            <label for="username">{{ $t('login.username') }}:</label>
            <input type="text" class="form-control" id="username" placeholder="User Name" v-model="username" required="" />
          </div>
          <div class="form-group text-left">
            <label for="password">{{ $t('login.password') }}:</label>
            <input type="password" class="form-control" id="password" v-model="password" required="" />
          </div>
        </form>
        <button class="btn btn-primary" v-on:click="login" v-bind:class="{ disabled: !validateForm() }">{{ $t('login.title') }}</button>
      </div>
    </div>
    <br/>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <button class="btn btn-danger" v-on:click="register">{{$t('login.register')}}</button>
      </div>
    </div>
    <br/>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <button class="btn btn-warning" v-on:click="forget">Forget password</button>
      </div>
    </div>
    <select class="locale" v-model="locale" v-on:change="changeLocale">
      <option value="zh">中文</option>
      <option value="en">English</option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data () {
    return {
      username: '',
      password: '',
      locale: localStorage.getItem('locale'),
    }
  },
  created () {
    this.locale = localStorage.getItem('locale')
    if (this.locale == null) {
      this.locale = 'en'
      localStorage.setItem('locale', this.locale)
    }
    this.$i18n.locale = this.locale
  },
  methods: {
    changeLocale: function () {
      console.log(this.locale)
      localStorage.setItem('locale', this.locale)
      this.$i18n.locale = this.locale
    },
    login: function () {
      // console.log(this.username)
      // console.log(this.password)
      var data = {
        username: this.username,
        password: this.password,
      }
      this.$http.post('http://localhost:8800/user/login', data).then(response => {
        // success callback
        console.log(response)
        this.$router.push('/index')
      }, response => {
        // error callback
        console.log(response)
        this.$router.push('/index')
      })
    },
    register: function () {
      this.$router.push('/register')
    },
    forget: function () {
      console.log('go to forget password page...')
      // this.$router.push('/forget')
    },
    isEmpty: require('../../util/strings.js').isEmpty,
    validateForm: function () {
      // check user name
      this.username = this.username.replace(/(^\s*)|(\s*$)/g, '')
      // console.log(this.username + '|')
      if (this.isEmpty(this.username)) {
        return false
      }

      // check password
      this.password = this.password.replace(/(^\s*)|(\s*$)/g, '')
      // console.log(this.password + '|')
      // console.log(this.repassword + '|')
      if (this.isEmpty(this.password)) {
        return false
      }

      return true
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  /* color: #42b983; */
}
.locale {
  position: fixed;
  right: 2%;
  top: 2%;
}
</style>



