<template>
  <div class="container">
    <h1>{{ $t('register.title') }}</h1>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <form>
          <div class="form-group text-left">
            <label for="username">{{ $t('register.username') }}:</label>
            <input type="text" class="form-control" id="username" placeholder="User Name" v-model="username" />
          </div>
          <div class="form-group text-left">
            <label for="password">{{ $t('register.password') }}:</label>
            <input type="password" class="form-control" id="password" v-model="password" v-on:input="changePassword" />
          </div>
          <div class="form-group text-left">
            <label for="password">{{ $t('register.repassword') }}:</label>
            <input type="password" class="form-control" id="repassword" v-model="repassword" v-on:input="changePassword" />
          </div>
          <div class="form-group text-left">
            <label>ID NO.:</label>
            <input type="text" class="form-control" placeholder="ID NO." v-model="idno" />
          </div>
          <div class="form-group text-left">
            <label>Phone NO.:</label>
            <input type="text" class="form-control" placeholder="Phone NO." v-model="phone" />
          </div>
          <div class="form-group text-left">
            <label>E-mail:</label>
            <input type="text" class="form-control" placeholder="E-mail" v-model="email" />
          </div>
          <div class="form-group text-left">
            <label>Nationality:</label>
            <select class="form-control" v-model="nationality">
              <option disabled value="">Please select one</option>
              <option v-for="nation in nations" v-bind:key="nation.value" v-bind:value="nation.value">{{ nation.name }}</option>
            </select>
          </div>
          <div class="form-group text-left">
            <label>Gender:</label>
            <select class="form-control" v-model="gender">
              <option disabled value="">Please select one</option>
              <option value="M">Male</option>
              <option value="F">Female</option>
            </select>
          </div>
        </form>
        <button class="btn btn-primary" v-on:click="register" v-bind:class="{ disabled: !validateForm() }">{{ $t('register.title') }}</button>
      </div>
    </div>
    <br/>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <button class="btn btn-success" v-on:click="back">{{$t('register.back')}}</button>
      </div>
    </div>
    <br/>
    <div class="row" v-show="showTip">
      <div class="col-sm-4 offset-sm-4 text-center">
        <p class="alert alert-danger">{{$t('register.tip')}}</p>
      </div>
    </div>
    <select class="locale" v-model="locale" v-on:change="changeLocale">
      <option value="zh">中文</option>
      <option value="en">English</option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'RegisterPage',
  data () {
    return {
      username: '',
      password: '',
      repassword: '',
      idno: '',
      phone: '',
      email: '',
      nationality: '',
      gender: '',
      showTip: false,
      locale: localStorage.getItem('locale'),
      nations: require('./nations.js').nations,
    }
  },
  created () {
    this.locale = localStorage.getItem('locale')
    if (this.locale == null) {
      this.locale = 'en'
      localStorage.setItem('locale', this.locale)
    }
    this.$i18n.locale = this.locale
  },
  watch: {
  },
  methods: {
    register: function () {
      if (!this.validateForm()) {
        return
      }
      var data = {
        username: this.username,
        password: this.password,
        idno: this.idno,
        phone: this.phone,
        email: this.email,
        nationality: this.nationality,
        gender: this.gender,
      }
      this.$http.post('http://localhost:8800/user/create', data).then(response => {
        // success callback
        console.log(response)
      }, response => {
        // error callback
        console.log(response)
      })
      var msg = 'Register successfully!'
      this.$router.push('/?msg=' + msg)
    },
    changeLocale: function () {
      console.log(this.locale)
      localStorage.setItem('locale', this.locale)
      this.$i18n.locale = this.locale
    },
    back: function () {
      this.$router.push('/')
    },
    changePassword: function () {
      if (this.password !== this.repassword) {
        this.showTip = true
      } else {
        this.showTip = false
      }
    },
    isEmpty: require('../../util/strings.js').isEmpty,
    validateForm: function () {
      // check user name
      this.username = this.username.replace(/(^\s*)|(\s*$)/g, '')
      // console.log(this.username + '|')
      if (this.isEmpty(this.username)) {
        return false
      }

      // check password
      this.password = this.password.replace(/(^\s*)|(\s*$)/g, '')
      this.repassword = this.repassword.replace(/(^\s*)|(\s*$)/g, '')
      // console.log(this.password + '|')
      // console.log(this.repassword + '|')
      if (this.isEmpty(this.password) || this.password !== this.repassword) {
        return false
      }

      // check id no.
      this.idno = this.idno.replace(/(^\s*)|(\s*$)/g, '')
      if (this.isEmpty(this.idno)) {
        return false
      }

      // check phone.
      this.phone = this.phone.replace(/(^\s*)|(\s*$)/g, '')
      if (this.isEmpty(this.phone)) {
        return false
      }

      // check email.
      this.email = this.email.replace(/(^\s*)|(\s*$)/g, '')
      if (this.isEmpty(this.email)) {
        return false
      }

      // check nationality.
      this.nationality = this.nationality.replace(/(^\s*)|(\s*$)/g, '')
      if (this.isEmpty(this.nationality)) {
        return false
      }

      // check gender.
      this.gender = this.gender.replace(/(^\s*)|(\s*$)/g, '')
      if (this.isEmpty(this.gender)) {
        return false
      }

      return true
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  /* color: #42b983; */
}
.locale {
  position: fixed;
  right: 2%;
  top: 2%;
}
</style>



